import config from './config'
import * as data from './data'

export default {
	name: "markdown",
	version: "1.0.0",
	description: "markdown",
	meta: data.getMeta(),
	components: [],
	config: config,
	load: (cb) => {
		require.ensure([], require => {
			cb(require('./component'), require('./action'), require('./reducer'))
		}, "markdown")
	}
}


